import type { FetchOptions } from 'ofetch'
import type { LumiClient } from '@/core/lumi-client'
import { ofetch } from 'ofetch'

export class FunctionsClient {
  readonly #lumi: LumiClient

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
  }

  public invoke(functionName: string, options: FetchOptions<'json'> = {}): Promise<any> {
    if (this.#lumi.auth.accessToken) {
      options.headers = {
        Authorization: `Bearer ${this.#lumi.auth.accessToken}`,
        ...options.headers,
      }
    }

    return ofetch(`/v1/functions/${this.#lumi.config.projectId}/${functionName}`, {
      baseURL: this.#lumi.config.apiBaseUrl,
      ...options,
    })
  }
}
